<template>
    <div class="detail">
        <div class="detail-tit">
            <van-nav-bar
            title="商品详情"
            left-text="返回" 
            @click-left="tojoin()"
            :fixed="true"
            />
        </div>
        <div class="detail-img">
            <img class="auto-img" :src="sclects.large_img" alt="">
        </div>
        <div class="detail-love clearfix">
            <span class="fl">{{sclects.name}}</span><span class="fr"><van-icon name="like" size="20px" :color="islike ? 'red' : '#ddd'" @click="clisave" /></span>
        </div>
        <div class="detail-sclect">
            <div class="sclect-title" v-for="(item,index) in sclects.rules" :key="index" >
                <span>{{item.title}}</span>
                <span @click="toggleScl(items,index)" :class="{active:items.isActive}" class="sclect-msg" v-for="(items,indexs) in item.tag" :key="indexs">{{items.name}}</span>
            </div>
            
        </div>
        <hr>
        <div class="detail-des">
            <div class="detail-des-ms">商品描述</div>
            <div class="des-text" v-for="(item,index) in sclects.desc" :key="index">{{item}}</div >
        </div>
        <div class="jsq clearfix">
            <span class="fl">￥{{sclects.price*value}}</span><van-stepper v-model="value" class="fr" theme="round" button-size="22" disable-input />
        </div>
        <div class="detail-bottom">
            <van-goods-action>
                <van-goods-action-icon @click='goShopcart() ' icon="cart-o" text="购物车" :badge="shopcartrows==0?'':shopcartrows" />
                <van-goods-action-button color="#f4b973" type="warning" text="加入购物车" @click='addshopcart' />
                <van-goods-action-button color="#593513" type="danger" @click='addshopcart(true)' text="立即购买" />
            </van-goods-action>
        </div>
    </div>
</template>

<script>
import '../assets/css/detail.less'
    export default {
        data(){
            return{
                islike:false,
                pid:"",
                sclects:[],
                value:1,
                typestr:"",
                shopcartrows:0
            }
        },
        created(){
            this.pid = this.$route.query.pid;
            
            this.getprolist(this.pid);
            this.findlike();
            this.findshopcart()
        },
        methods:{
            tojoin(e){
                this.$router.go(-1)
            },
            toggleScl(items,indexs){
                
                if(items.isActive){
                    return
                }
                
                for(let j = 0 ; j <this.sclects.rules[indexs].tag.length ; j ++){
                    if(this.sclects.rules[indexs].tag[j].isActive){
                        
                        this.sclects.rules[indexs].tag[j].isActive = false
                    }
                }
                
                items.isActive = true
            },
            getprolist(e){
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    url:"/productDetail",
                    method:"GET",
                    params:{
                        appkey:this.appkey,
                        pid:e
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    let data = result.data.result[0];
                    data.desc = data.desc.split(/\n/);
                    if(result.data.code == 600){
                        let proRules = ["tem","milk","sugar","cream"]
                        let rules = [];
                        for(let i = 0 ; i < proRules.length ; i ++){
                            if(data[proRules[i]] != ""){
                                let rule = {
                                    title:data[proRules[i]+"_desc"],
                                    tag:[]
                                };
                                let tag = data[proRules[i]].split("/");
                                tag.forEach((item,i)=>{
                                    let t = {
                                        name: item,
                                        isActive: i == 0
                                    };
                                    rule.tag.push(t);
                                })
                                
                                rules.push(rule);
                                

                            }
                        }
                        

                        data.rules = rules;

                        this.sclects = data;
                        
                    }
                    
                }).catch(err => {
                    this.$toast.clear();
                    
                })
                
            },
            //点击收藏
            clisave(){
                
                let url = this.islike ? '/notlike' : '/like';
                let tokenString = localStorage.getItem('login_Tk');
                
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method: 'POST',
                    url,
                    data: {
                        appkey: this.appkey,
                        pid: this.pid,
                        tokenString
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == 800 || result.data.code == 900) {
                    //收藏成功
                    if(result.data.code == 800){
                        this.islike = true
                    }else{
                        this.islike = false
                    }
                    
                    this.$toast(result.data.msg);
                    } else {
                    //如果token无效，则跳转到登录页面
                    this.$router.push({name: 'Login'});
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })
            },
            findlike(){
                let tokenString = localStorage.getItem('login_Tk');
                //开启加载提示
                
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method:"GET",
                    url:"/findlike",
                    params:{
                        appkey:this.appkey,
                        pid:this.pid,
                        tokenString
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                     if (result.data.code == 1000) {

                        this.islike = result.data.result.length > 0;

                    } else {
                        // 如果token无效，则跳转到登录页面
                        this.$router.push({name: 'Login'});
                    }
                }).catch(err=>{
                    this.$toast.clear()
                    
                })
            },
            findshopcart(){
                let tokenString = localStorage.getItem('login_Tk');

                //加载
                this.$toast.loading({
                    message:"加载中",
                    forbidClick:true,
                    duration:0
                })

                this.axios({
                    method:"GET",
                    url:"/findAllShopcart",
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    if(result.data.code==5000){
                        this.shopcartrows = result.data.result.length;
                        
                    }
                     
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
               
            },
            addshopcart(pake){
                this.typestr=""
                
                for(let i = 0 ; i < this.sclects.rules.length ; i ++){
                    for(let j = 0 ; j < this.sclects.rules[i].tag.length ; j ++){
                        
                        if(this.sclects.rules[i].tag[j].isActive){
                            
                            this.typestr += this.sclects.rules[i].tag[j].name+"/";
                        }
                    }
                }
                this.typestr = this.typestr.substr(0, this.typestr.length - 1);
                let tokenString = localStorage.getItem('login_Tk');
                this.$toast.loading({
                    message: '加载中',
                    forbidClick: true,
                    duration: 0
                });
                this.axios({
                    method:"POST",
                    url:"/addShopcart",
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        pid:this.pid,
                        count:this.value,
                        rule:this.typestr
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    if(result.data.code==3000){
                        if(result.data.status==1){
                            this.shopcartrows++
                        }
                        if(pake === true){
                            this.$router.push({name:'Pay',query:{sids:result.data.sid}})
                        }
                        this.$toast(result.data.msg);
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })
            },
            goShopcart() {
                this.$router.push({name: 'Shopcart'});
            }
        }

    }
    
</script>

<style lang="less" scoped>

</style>